@keyframes pulse {
  25% {
    text-shadow: 0px 0px 10px rgba(255, 154, 0, 1),
      0px 0px 5px rgba(255, 154, 0, 0.4);
  }
  50% {
    text-shadow: 0px 0px 3px rgba(255, 154, 0, 0.4),
      0px 0px 10px rgba(255, 154, 0, 1);
  }
  75% {
    text-shadow: 0px 0px 5px rgba(255, 154, 0, 1),
      0px 0px 15px rgba(255, 154, 0, 1);
  }
  100% {
    text-shadow: 0px 0px 5px rgba(255, 154, 0, 0.7),
      0px 0px 15px rgba(255, 154, 0, 0.7);
  }
}

.title {
  font-size: 2rem;
  color: #ff9a00;
  text-shadow: 0px 0px 5px rgba(255, 154, 0, 0.7),
    0px 0px 15px rgba(255, 154, 0, 0.7);
  animation-name: pulse;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}

.display {
  margin: 2rem auto;
  color: #eaeaea;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

p {
  padding: 1rem;
  border-radius: 6px;
  letter-spacing: 1px;
}

.exx {
  color: #ff2e63;
}

.ohh {
  color: #08d9d6;
}

.neither {
  color: #eaeaea;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.75),
    0px 0px 30px rgba(255, 255, 255, 0.5),
    0px 0px 50px rgba(255, 255, 255, 0.25);
}

.reset {
  padding: 0.5rem;
  margin-top: 2rem;
  border-radius: 6px;
  font-size: 1rem;
  color: #eaeaea;
  font-weight: bolder;
  letter-spacing: 1px;
  background: transparent;
  border: 2px solid #eaeaea;
}
