@keyframes highlight {
  10% {
    box-shadow: inset 0px -300px rgba(255, 255, 255, 0.5);
  }
}

.btn:hover,
.btn:active {
  animation-name: highlight;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
}

.btn {
  background: #252a34;
  border: none;
  font-size: 4rem;
  font-weight: bolder;

  cursor: pointer;
  outline: none;
}

.p1 {
  color: #ff2e63;
  text-shadow: 0px 0px 5px rgba(255, 0, 0, 0.9),
    0px 0px 15px rgba(255, 0, 0, 0.9);
  background: #801731;
}

.p2 {
  color: #08d9d6;
  text-shadow: 0px 0px 5px rgba(8, 217, 214, 0.9),
    0px 0px 15px rgba(8, 217, 214, 0.9);
  background: #045958;
}
